.video-embed__container {
    position: relative;
    padding-bottom: 56.25%;
    overflow: hidden;
    max-width: 100%;
    height: auto;

    & > iframe,
    & > object,
    & > embed {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
    }

    &.embed-container__map {
        & > iframe {
            @include breakpoint(medium) {
                height: 75%;
            }
            
            top: -55px;
        }
    }
}